html{box-sizing:border-box;font-size:16px}*,*:before,*:after{box-sizing:inherit}body{min-height:100vh;margin:0;padding:0;background:#222;color:#f9f9f9;scroll-behavior:smooth;text-rendering:optimizeSpeed;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.App{max-width:1280px;padding:56px 16px;margin:0 auto;display:grid;grid-gap:16px;gap:16px;justify-items:stretch;align-items:stretch;grid-template-columns:1fr;grid-template-rows:auto;grid-template-areas:"header" "view" "ui" "footer"}@media (min-width: 991px){.App{grid-template-columns:auto 400px;grid-template-rows:auto;grid-template-areas:"header header" "view ui" "footer"}}.app-header{grid-area:header;grid-column:1 / 2;grid-row:1 / 2;display:flex;flex-flow:column wrap;justify-content:center;align-items:center}@media (min-width: 991px){.app-header{grid-column:1 / 3;grid-row:1 / 2;flex-flow:row wrap;justify-content:flex-start}}.app-header .logo{cursor:pointer}.app-header .logo .logoImg{height:4rem;margin:0 0 1rem 0}@media (min-width: 991px){.app-header .logo .logoImg{margin:0 1rem 0 0}}.app-header .titleText{margin:0;font-size:1.5rem;font-family:"Roboto", sans-serif;font-weight:bold;letter-spacing:0.1em;text-transform:uppercase}.app-view{grid-area:view;grid-column:1 / 2;grid-row:2 / 3}@media (min-width: 991px){.app-view{grid-column:1 / 2;grid-row:2 / 3}}.app-ui{grid-area:ui;grid-column:1 / 2;grid-row:3 / 4}@media (min-width: 991px){.app-ui{grid-column:2 / 3;grid-row:2 / 3}}.app-footer{grid-area:footer;grid-column:1 / 2;grid-row:4 / 5;justify-self:center;font-size:0.75em;color:#666}@media (min-width: 991px){.app-footer{grid-column:1 / 3;grid-row:3 / 4;justify-self:end}}input{-webkit-appearance:none;-moz-appearance:none;appearance:none}input[type="range"]{-webkit-appearance:none;width:100%;height:20px;margin:0;background:transparent}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{width:100%;height:4px;cursor:pointer;animate:200ms;background:#ababab}input[type="range"]::-webkit-slider-thumb{box-shadow:2px 2px 4px rgba(0,0,0,0.25);height:20px;width:20px;border-radius:10px;background:#333333;cursor:pointer;-webkit-appearance:none;margin-top:-8px}input[type="range"]:focus::-webkit-slider-runnable-track{background:#0037ff}input[type="range"]::-moz-range-track{width:100%;height:4px;cursor:pointer;animate:200ms;background:#ababab}input[type="range"]::-moz-range-thumb{box-shadow:2px 2px 4px rgba(0,0,0,0.25);height:20px;width:20px;border-radius:10px;background:#333333;cursor:pointer;margin-top:0;border:0}input[type="range"]::-ms-track{width:100%;height:4px;cursor:pointer;animate:200ms;background:transparent;border-color:transparent;border-width:8px 0;color:transparent}input[type="range"]::-ms-fill-lower{background:#ababab}input[type="range"]::-ms-fill-upper{background:#ababab}input[type="range"]::-ms-thumb{box-shadow:2px 2px 4px rgba(0,0,0,0.25);height:20px;width:20px;border-radius:10px;background:#333333;cursor:pointer;margin-top:0}input[type="range"]:focus::-ms-fill-lower{background:#0037ff}input[type="range"]:focus::-ms-fill-upper{background:#0037ff}a:link{color:#0037ff}a:visited{color:#0037ff}a:hover,a:active{color:#ff003d}input[type="number"]:focus{outline:2px solid #0037ff}button:focus{outline:2px solid #0037ff}

